import { makeStyles } from "@material-ui/core/styles";
import image from "../assets/image.jpeg";
import Typography from "@material-ui/core/Typography";
import blurImg from "../assets/blurImg.jpg";
import { useState, useEffect, useRef } from "react";

const useStyles = makeStyles((theme) => ({
  root: {
    display: "flex",
    width: "100vw",
    flexDirection: "row",
    [theme.breakpoints.down("sm")]: {
      flexDirection: "column"
    }
  },
  image: {
    width: 335
  },

  imageContainer: {
    width: 335,
    height: 256,
    margin: "0 30px 30px 0",
    overflow: "hidden",
    "@media (max-width:345px)": {
      width: "100vw"
    }
  },
  blurImg: {
    position: "absolute",
    zIndex: -1,
    opacity: 1,
    width: 335,
    height: 256,
    backgroundRepeat: "no-repeat",
    backgroundSize: "cover",
    filter: "blur(1px)",
    transition: "opacity ease-in 1000ms",
    backgroundImage: `url(${blurImg})`,
    margin: "0 30px 30px 0",
    "@media (max-width:345px)": {
      width: "100vw"
    }
  },
  description: {
    width: 600,
    "@media (min-width:696px)": {
      width: 350
    },
    "@media (min-width:1267px)": {
      width: 600
    },
    [theme.breakpoints.down("sm")]: {
      width: "calc(100% - 10px)",
      padding: 10
    }
  },
  list: {
    lineHeight: 1.8,
    fontSize: "0.875rem",
    fontWeight: 500,
    letterSpacing: "0.00714em"
  }
}));

const useImageLoaded = () => {
  const [loaded, setLoaded] = useState(false);
  const ref = useRef();

  const onLoad = () => {
    setLoaded(true);
  };

  useEffect(() => {
    if (ref.current && ref.current.complete) {
      onLoad();
    }
  });

  return [ref, loaded, onLoad];
};

export default function BioComponent() {
  const classes = useStyles();

  const [ref, loaded, onLoad] = useImageLoaded();

  return (
    <div className={classes.root}>
      <div className={classes.imageContainer}>
        <div
          className={classes.blurImg}
          {...(loaded && { style: { opacity: "0" } })}
        />

        <img
          onLoad={onLoad}
          className={classes.image}
          src={image}
          alt="irina karpova"
          ref={ref}
        />
      </div>

      <div className={classes.description}>
        <Typography variant="subtitle2" align="left" gutterBottom={true}>
          I graduated from Saint Petersburg State University in 2008 with a B.A.
          in Information Technology. The program splits strong skills in
          Information technology and Design thinking.
        </Typography>
        <Typography variant="subtitle2" align="left" gutterBottom={true}>
          Right After my graduation, I was lucky to join an international team
          working on the book Encyclopedia of Russian Avangard. I was
          responsible for the WEB version of the book. That was the first
          scalable project that gave me a unique experience in software
          development.
        </Typography>
        <Typography variant="subtitle2" align="left" gutterBottom={true}>
          Today, my focus covers responsible for Developing the User Interface:
        </Typography>
        <ul>
          <li className={classes.list}>Designing User Interface Elements</li>
          <li className={classes.list}>
            Displaying Content across various devices
          </li>
          <li className={classes.list}> Handling User Interaction</li>
          <li className={classes.list}>
            Input and Submiting data to server side
          </li>
          <li className={classes.list}> Network Requests</li>
        </ul>
        <Typography variant="subtitle2" align="left">
          For the last few years, I responsible for the front-end part of the
          development of affiliate software. My workflow includes translating
          UX/UI wireframes to interactive components into further implementation
          with the back-end part.
        </Typography>
      </div>
    </div>
  );
}
