import { createContext, useState, useEffect } from "react";
import { createMuiTheme } from "@material-ui/core/styles";
import { lightTheme, darkTheme } from "../themes";

const AppStateProvider = ({ children }) => {
  const [theme, setTheme] = useState(
    localStorage.getItem("storeTheme") === "lightTheme"
  );
  const currentTheme = createMuiTheme(theme ? lightTheme : darkTheme);
  const [checked, setChecked] = useState(
    localStorage.getItem("storeTheme") === "darkTheme"
  );

  const handleChangeTheme = () => {
    setTheme(!theme);
    setChecked(!checked);
  };

  useEffect(() => {
    window.localStorage.setItem(
      "storeTheme",
      theme ? "lightTheme" : "darkTheme"
    );
  }, [currentTheme, checked, theme]);
  return (
    <AppStateContext.Provider
      value={{ checked, handleChangeTheme, currentTheme }}
    >
      {children}
    </AppStateContext.Provider>
  );
};

export const AppStateContext = createContext({
  currentTheme: lightTheme,
  checked: false,
  handleChangeTheme: () => {}
});

export default AppStateProvider;
